 <style scoped  lang="scss">
.swiper-slide {
  position: relative;
}
.product-meta-data {
  position: absolute;
  bottom: 25%;
  display: flex;
  width: 100%;
  height: 50px;
  justify-content: center;
  button {
    border: 0;
  }
}
.shop-icon{
  color: #90ac10;
}
.product-meta-data a,
.product-meta-data button {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: #ffffff;
  box-shadow: 0 3px 15px 0 rgba(0, 0, 0, 0.2);
  text-align: center;
  font-size: 16px;
  margin: 0 10px;
  line-height: 42px;
  opacity: 0;
  transition: all 0.5s;
}

.product-meta-data a:hover,
.product-meta-data button:hover {
  background-color: #90ac10;
    color: #fff;
  .shop-icon{
  color: #fff;
  }
}

.card {
  border: 0;
  margin: 0 10px;
}
.card:hover a,
.card:hover button {
  opacity: 1;
}
</style>
<template>
  <div>
    <div v-swiper:mySwiper="swiperOption">
      <div class="swiper-wrapper">
        <div class="swiper-slide" v-for="(item,index) in products" :key="index">
          <div class="card">
            <img :src="item.imageUrl" class="card-img-top" alt />
            <div class="product-meta-data">
              <router-link  :to="'/products_detail/'+item.id" data-toggle="tooltip" data-placement="top" title="">
                <i class="fas fa-search"></i>
              </router-link>
              <button
                ref="shopBtn"
                :data-num="index"
                type="button"
                data-toggle="tooltip"
                data-placement="top"
                title=""
              >
                <i class="fas fa-shopping-cart shop-icon" :data-num="index"></i>
              </button>
            </div>
            <div class="card-body">
              <h5 class="card-title">{{item.title}}</h5>
              <p class="card-text">$ {{item.price}}</p>
            </div>
          </div>
        </div>
      </div>
      <div class="swiper-pagination"></div>
      <div class="swiper-button-next"></div>
      <div class="swiper-button-prev"></div>
      <div class="swiper-scrollbar"></div>
    </div>
    <!-- <div v-for="(item,index) in products" :key="index">{{item.description}}</div> -->
  </div>
</template>

<script>
import $ from 'jquery';
import { Swiper, SwiperSlide, directive } from 'vue-awesome-swiper';
import 'swiper/css/swiper.css';
import { mapGetters, mapActions } from 'vuex';

export default {
  name: 'carrousel',
  data() {
    const vm = this;
    return {
      // swiper 
      swiperOption: {
        autoplay: {
          // 
          delay: 300000,
          disableOnInteraction: false,
        },
        on: {
          click(e, qty) {
            if (!e.target.dataset.num) return;
            //  DOM  data 
            const dataIndex = e.target.dataset.num;
            //  Vue products[]
            const products = vm.products[dataIndex];
            const { id } = products;
            vm.$store.dispatch('addtoCart', { id, qty });
          },
        },
        speed: 1000, // 
        loop: true, // 
        slidesPerView: 2, //  slider 
        spaceBetween: 30, // slider 
        breakpoints: {
          // 
          576: {
            spaceBetween: 60,
            slidesPerView: 3, //  slider 
          },
        },
        pagination: {
          el: '.swiper-pagination',
          clickable: true,
        },
        navigation: {
          nextEl: '.swiper-button-next', // 
          prevEl: '.swiper-button-prev', // 
        },
      },
    };
  },
  methods: {
    ...mapActions(['getProducts', 'addtoCart']),
    //  tooltip 
    tooltipList() {
      $(() => {
        $('[data-toggle="tooltip"]').tooltip();
      });
    },
  },
  computed: {
    ...mapGetters(['isLoading', 'products']),
  },
  mounted() {
    //  vuex 
    this.tooltipList();
    this.getProducts();
  },
  components: {
    Swiper,
    SwiperSlide,
  },
  directives: {
    swiper: directive,
  },
};
</script>
